export default {
  data(){
    return {
      file: '',
      value: ''
    }
  },
  methods:{
    hanldeClick: function(){
      var file = this.$refs.camera.files[0]
      var reader = new FileReader();
      reader.readAsDataURL(file);
      var _that = this;
      reader.onload=function (oFREvent) {  
        _that.file = oFREvent.target.result; 
      };
    }
  }

};



