export default {
  name: 'page-checklist',

  data() {
    return {
      options1: [],
      options2: [],
      options3: [],
      options4: [],
      value1: "选中禁用的值",
      value2: '选项A',
      value3: '选项A',
      value4: '',
      value: "",
    };
  },
  methods: {
      blank(e,val){
        console.log(e,val,'你点击了！')
      }
  },

  created() {
    this.options = [
      {
        id:"1",
        label:"男"
      },
      {
        id:"2",
        label:"女"
      },
      {
        id:"3",
        label:"禁用",
        disabled: true
      }
    ]
    this.options1 = ['选项A', '选项B', '选项C'];
    this.options3 = ['选项A', '选项B', '选项C', '选项D'];
    this.options4 = ['选项A', '选项B', '选项C', '选项D'];
    this.options2 = [
      {
        label: '被禁用',
        value: '值F',
        disabled: true,
      },
      {
        label: '选中禁用',
        value: '选中禁用的值',
        disabled: true
      },
      {
        label: '选项A',
        value: '值A'
      },
      {
        label: '选项B',
        value: '值B'
      }
    ];
  }
};



